import React, { ReactNode, useRef } from 'react';
import {
    OverlayTriggerProps as OverlayTriggerAriaProps,
    useInteractOutside,
} from 'react-aria';
import { useOverlayTrigger } from 'react-aria';
import { OverlayTriggerProps as OverlayTriggerStateProps } from 'react-stately';
import { useOverlayTriggerState } from 'react-stately';
import classNames from 'classnames';

import Button from '../Button/Button';
import { Dialog } from '../Dialog';
import { SvgInfoFill } from '../Icons';
import Popover from '../Popover/Popover';
import { Text } from '../Typography';

import styles from './InfoHint.module.scss';

export interface InfoHintProps {
    children?: ReactNode | ReactNode[];
    inverted?: boolean;
    triggerAriaLabel?: string;
    isNonModal?: boolean;
}

export const InfoHint = ({ children, inverted, ...props }: InfoHintProps) => {
    const ref = useRef(null);
    const popoverRef = useRef(null);
    const state = useOverlayTriggerState(props as OverlayTriggerStateProps);
    const { triggerProps } = useOverlayTrigger(
        props as OverlayTriggerAriaProps,
        state,
        ref
    );

    const { triggerAriaLabel, isNonModal = false } = props;

    useInteractOutside({
        ref: popoverRef,
        onInteractOutside: state.close,
    });

    return (
        <>
            <Button
                {...triggerProps}
                ref={ref}
                aria-label={triggerAriaLabel}
                variant="secondary"
                isIcon
                inverted={inverted}
                className={classNames(styles.button, {
                    [styles.inverted]: inverted,
                })}
            >
                <SvgInfoFill aria-hidden="true" />
            </Button>
            {state.isOpen && (
                <Popover
                    {...props}
                    triggerRef={ref}
                    state={state}
                    offset={6}
                    popoverRef={popoverRef}
                    className={classNames(styles.popover, {
                        [styles.inverted]: inverted,
                    })}
                    isNonModal={isNonModal}
                >
                    <Dialog className={styles.dialog}>
                        <Text size="small">{children}</Text>
                    </Dialog>
                </Popover>
            )}
        </>
    );
};
